@import "../../../scss/main.scss";

.search {
  display: flex;
  min-width: 19.2em;
  height: 4.8rem;
  padding: 4px;
  border: 1px solid $light-gray;
  border-radius: 0.6rem;

  ::placeholder {
    font-size: 1.6rem;
  }

  > input {
    width: 40rem;
    max-width: 100%;
    height: 100%;
    padding-left: 0.8rem;
    border: 0;

    &:active,
    &:focus {
      background-color: #eee; // rm to prod
    }
  }

  > button {
    flex: 0 0 4.7rem;
  }
}

@media (max-width: 888px) {
  .search {
    flex-basis: 100%;
    order: 1;
    margin: 0;
    margin-top: 3.8rem;
  }

  .search > input {
    flex-basis: 100%;
  }
}
