@import "../../scss/main"; // show main.scss > // 5. Components

.header {
  display: flex;
  flex: 1 1 0%;
  width: 100%;
  max-width: 115.2rem;
  margin: 0 auto;
  background-color: #fff;
}

.header__logo {
  margin: 0;
  margin-top: 2.4rem;
}

.header__search {
  margin: 0 2em;
  margin-top: 3.2rem;
  margin-left: 8.4em;
}

.header__quickorder {
  display: flex;
  flex: 0 0 18rem;
  margin-top: 3.2rem;
  margin-right: 1.6rem;
  margin-left: auto;
  padding: 0 1.6rem;
  font-size: $font-size-small2;
  font-weight: normal;
  text-align: left;

  > svg {
    margin-right: 0.8em;
    fill: $color-brand-light;
  }

  > svg,
  > span {
    align-self: center;
  }
}

.header__account,
.header__cart {
  flex-shrink: 0;
  min-width: 8.4rem;
  margin-top: 2.4rem;
  padding: 0 0.8em;
  transition: background-color 0.4s, fill 3.4s;
  font-size: $font-size-medium;
  font-weight: bold;

  .icon__txt {
    display: block;
    margin-top: 0.8rem;

    &--mob {
      display: none;
    }
  }

  &:hover {
    background-color: #333;

    .icon {
      fill: #fff;
    }

    span {
      color: #fff;
    }
  }

  .icon {
    width: auto;
    height: 32px;
  }

  .icon--primary-gradient {
    fill: $color-brand; // replace with svg gradient
    fill: url("#colBrandToColBrandMed");
  }
}

// @media (max-width: 768px) {
@media (max-width: 888px) {
  .header {
    flex-wrap: wrap;

    &__search {
      flex-basis: 100%;
      order: 1;
      margin: 0;
      margin-top: 3.8rem;
    }
  }

  .header__search > input {
    flex-basis: 100%;
  }
}

@media (max-width: 600px) {
  .header {
    width: calc(100% - 1.6rem);
    margin: 0 0.8rem;
  }

  .header__burger {
    margin-top: 1.6rem;
    margin-right: 1.6rem;
  }

  .header__logo {
    align-self: flex-start;
    margin-top: 1.6rem;
    margin-right: auto;
  }

  .header__quickorder {
    flex-basis: 100%;
    order: 2;
    margin-top: 1.6rem;
  }

  .header__search {
    margin-top: 1.6rem;
  }

  .header__account,
  .header__cart {
    min-width: max-content;
    margin-top: 1.6rem;
    font-size: 0.8rem;
    text-transform: uppercase;

    .icon__txt {
      display: block;

      &--desk {
        display: none;
      }
    }

    .icon {
      height: 24px;
    }
  }
}
