@import "../../../scss/main.scss";

.burger {
  display: none;
  font-size: $font-size-tiny;
  font-weight: bold;
  text-transform: uppercase;

  > input {
    display: none;
  }

  > label {
    cursor: pointer;
    user-select: none;
  }

  input:checked + label > span:nth-child(2) {
    width: 0;
    transition: all 0.3s ease-in-out;
    opacity: 0;
  }

  input:checked + label > span:nth-child(1) {
    position: relative;
    top: 8px;
    transform: rotateZ(45deg);
    transition: all 0.3s ease-in-out;
  }

  input:checked + label > span:nth-child(3) {
    position: relative;
    top: -6px;
    transform: rotateZ(-45deg);
    transition: all 0.3s ease-in-out;
  }

  span {
    display: block;
    width: 24px;
    height: 3px;
    margin-bottom: 4px;
    transition: all 0.3s ease-in-out;
    border-radius: 4px;
    background: $color-base;
  }
}

@media (max-width: 600px) {
  .burger {
    display: block;
    margin-top: 1.6rem;
    margin-right: 1.6rem;
  }
}
