@import "../../../scss/main.scss"; // show main.scss > // 5. Components

.logo {
  flex: 0 0 auto;
  align-self: center;
  max-width: 18rem;
  margin: 0;

  a {
    display: flex;
    flex-direction: column;

    &:hover {
      text-decoration: none;
    }
  }

  svg {
    width: 18rem;
    height: auto;
  }

  .baseline {
    color: $medium-gray;
    // compose: medium-gray from "../../scss/main";
    font-size: $font-size-small;
    font-weight: normal;
    text-align: center;
  }
}

@media (max-width: 600px) {
  .logo {
    align-self: flex-start;

    svg {
      width: 104px;
      height: auto;
    }

    a > span {
      display: none;
    }
  }
}
