@import "../../../scss/main.scss";
/*! src/scss/components/atoms/button/_button.scss */
.btn {
  transition: background-color 0.4s;
  cursor: pointer;

  &--primary {
    border: 0;
    border-radius: 6px;
    background-color: $color-brand;
    background-image: linear-gradient($color-brand-medium, $color-brand);
    color: #fff;
    font-size: $font-size-small2;

    &:hover {
      background: #000;
    }
  }

  &--secondary {
    // composes: btn--primary;
    @extend .btn--primary;
    background-color: $color-secondary;
    background-image: linear-gradient($color-secondary-light, $color-secondary);
  }

  &--small {
    height: 3.8rem;
    padding: 0 0.8rem;
  }

  &--medium {
    height: 4.8rem;
    padding: 0 1.6rem;
  }

  &--pad-small {
    padding: 0 0.8rem;
  }

  &--pad-medium {
    padding: 0 1.6rem;
  }

  &__link {
    // composes: btn; // compose === local @extend
    display: flex;
    align-items: center; /* Vertical align */
    justify-content: center; /* horizontal align */
    width: max-content;
  }
}
